import React, { useContext, useEffect, useRef, useState } from "react";
import StateContext from "../../context/state";
import styles from "./ParticlesLayer.module.scss";
import PathContext from "../../context/path";
import Particles from '../../utils/particles';
const ParticlesLayer = (props) => {

  const path = useContext(PathContext);
  const stateApp = useContext(StateContext);
  const [partElement, setPartElement] = useState(null);
  const { type } = props;
  const { stage, transition, isMobile } = stateApp;
  const refWrap = useRef(null);
  const [currentStage, setCurrentStage] = useState(0);
  const [imgStage, setImgStage] = useState(-1);
  const [reverse, setReverse] = useState(false);
  const [init, setInit] = useState(false);
  const data = {
    'main': [
      [0, '/img/source/mobile/1-1.webp'],
      [1, '/img/source/mobile/1-2.webp'],
      [2, '/img/source/mobile/1-3.webp'],
      [3, '/img/source/mobile/1-4.webp'],
      [4, '/img/source/mobile/1-5.webp'],
      [5, '/img/source/mobile/1-6.webp'],
      [6, '/img/source/mobile/1-7.webp']
    ],
    'services': [
      [7, '/img/source/mobile/2-1.webp'],
      [8, '/img/source/mobile/2-2.webp'],
      [9, '/img/source/mobile/2-3.webp'],
      [10, '/img/source/mobile/2-4.webp'],
      [11, '/img/source/mobile/2-5.webp'],
      [12, '/img/source/mobile/2-6.webp']
    ],
    'compare': [
      [13, '/img/source/mobile/3-1.webp'],
      [14, '/img/source/mobile/3-2.webp']
    ],
  };
  useEffect(() => {
    const wrap = refWrap.current;
    let element;
    // if (!isMobile) {
      const callback = () =>{
        return setInit(true);
      }
      element = new Particles(wrap, type, callback);
      setPartElement(element);
    // }
    return () => {
      wrap.innerHTML = '';
      element = null;
      setPartElement(null)
    }
  }, [type, isMobile]);
  useEffect(() => {
    let diff = currentStage > stage;
    if (!transition && diff) {
      setReverse(true);
    } else {
      setReverse(false);
    }
  }, [currentStage, stage, transition]);
  useEffect(() => {
    let diff = currentStage - stage;
    if (reverse && !transition) {
      setCurrentStage(stage);
    }

    if (diff < 0 || (stage >= 1 && stage < 10) || stage >= 11) {
      setCurrentStage(stage);
    }
  }, [stage, currentStage, reverse, transition]);

  useEffect(() => {

    if (type === 'main') {
      switch (currentStage) {
        case 0:
          setImgStage(0);
          break;
        case 1:
          setImgStage(1);
          break;
        case 2:
          setImgStage(2);
          break;
        case 10:

          setImgStage(3);
          break;
        case 15:

          setImgStage(4);
          break;
        case 16:

          setImgStage(5);
          break;
        case 38:
          if (!isMobile) {
            setImgStage(6);
          }
          break;
        case 42:
          if (isMobile) {
            setImgStage(6);
          }
          break;

        default:
          setImgStage(-1);
          break;

      }
    }
  }, [isMobile, partElement, currentStage, type]);

  useEffect(() => {
    if (type === 'services') {
      switch (currentStage) {
        case 0:
          setImgStage(7);
          break;
        case 2:
          setImgStage(8);
          break;
        case 3:
          setImgStage(9);
          break;
        case 4:
          setImgStage(10);
          break;
        case 5:
          setImgStage(11);
          break;
        case 8:
          setImgStage(12);
          break;
        case 9:
          setImgStage(10);
          break;
        case 10:
          setImgStage(11);
          break;
        case 14:
          setImgStage(11);
          break;
        case 15:
          setImgStage(11);
          break;
        default:
          setImgStage(-1);
          break;
      }
    }
  }, [currentStage, partElement, type]);

  useEffect(() => {
    if (type === 'compare') {
      switch (currentStage) {
        case 5:
          setImgStage(13);
          break;
        case 6:
          setImgStage(14);
          break;
        default:
          setImgStage(-1);
          break;
      }
    }
  }, [currentStage, partElement, type]);

  // useEffect(()=>{
  //   if (partElement !== null) {
      
  //     console.log(partElement.webgl.particles.isInit);
  //   }
  // }, [partElement, imgStage]);
  useEffect(() => {
      if (partElement !== null) {
        if (imgStage >= 0) {
          partElement.webgl.goto(imgStage);
        } else {
          if (partElement.webgl.particles.isInit && init) {
            partElement.webgl.hide();
          }
        }
        
      // console.log(init);
      }
  }, [imgStage, partElement, isMobile, init]);

  return (
    <>
      <div className={styles.wrap} ref={refWrap} key={`stage${isMobile}`}>
      </div>
    </>
  );

}

export default ParticlesLayer;
